.moviesGallery {
  display: grid;
  max-width: calc(100vw - 48px);
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
  grid-gap: 16px;
  margin-top: 0;
  margin-bottom: 0;
  padding: 0;
  list-style: none;
  margin-left: auto;
  margin-right: auto;
}
.item {
  min-height: 585px;
  border-radius: 2px;
  box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2),
    0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 2px 1px -1px rgba(0, 0, 0, 0.12);
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.image {
  width: 100%;
  height: auto;
  object-fit: cover;
}

.item:hover {
  transform: scale(1.03);
  cursor: zoom-in;
}
.title {
  padding: 5px 8px;
  font-size: 18px;
  font-weight: 600;
  color: orangered;
}
